* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  outline: none;
  border: none;
  list-style: none;
}

a, a:visited, a:active {
  text-decoration: none;
  outline: none;
}

p {
  margin-top: 0;
}

body {
  background: #444;
}
body h1 {
  text-transform: uppercase;
}